/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgb(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  top: -5px;
  right: 105%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
  position: absolute;
  z-index: 1;
  text-transform: capitalize;
  font-family: 'Londrina Solid', sans-serif;
  size: 1.3rem;
  font-weight: 100;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

/* Add arrow below tooltip */
.tooltip .tooltiptext::after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: 100%; /* To the right of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent black;
}

.advantage {
  padding: 3px;
  font-size: 1.3rem;
}

.tribe-advantage-container img {
  height: 1.3rem;
  width: auto;
  filter: drop-shadow(3px 3px 2px black) drop-shadow(-1px -1px 4px black);
}

.tribe-advantage-container {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  color: white;
  text-shadow: 3px 3px 5px black, -1px -1px 8px black;
  flex-direction: column;
  z-index: 100;
  backdrop-filter: none;
}
