/* Add a black background color to the top navigation */
.navbar {
    background-color: #333;
    z-index: 100;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    align-items: center;
    grid-template-areas: "season episode title";
  }

#season-select {
  grid-area: season;
  height: 40px;
}
  
.episode-selector {
  display: grid;
  grid-template-columns: 1fr 124px 1fr;
  align-items: center;
  justify-self: center;
  align-self: stretch;
  height: 40px;
  font-family: 'Londrina Solid', sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  grid-area: episode;
}

.episode-selector h2 {
  display: inline;
  font-weight: 400;
  font-size: 1.8rem;
  justify-self: center;
  /* Override other h2 properties */
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

.episode-arrow {
  padding: 0px 9px;
  font-size: 2rem;
}

.episode-arrow.right {
  justify-self: start;
}

.episode-arrow.left {
  justify-self: end;
}

.hidden {
  visibility: hidden;
}

.navbar h1 {
  margin: 0px;
  justify-self: end;
  font-family: 'Survivant';
  font-size: 1.5rem;
  font-weight: 500;
  color: rgb(255,255,255,0.8);
  padding-right: 5px;
  grid-area: title;
}

/*------------------*/
/* DROPDOWN STYLING */
/*------------------*/

/* Navbar container */
.navbar {
  background-color: #333;
  font-family: Arial;
}

div i {
  font-size: .8em;
}

.season-title-icon i {
  padding-left: 8px;
  padding-right: 8px;
}

.season-title-icon {
  justify-self: start;
}

/* Links inside the navbar */
.navbar div.season-option {
  font-size: 16px;
  color: white;
  padding: 14px 16px;
  text-decoration: none;
}

/* The dropdown container */
.dropdown {
  overflow: hidden;
}

/* Dropdown button */
.dropdown .dropbtn {
  font-size: 16px; 
  border: none;
  outline: none;
  color: rgb(255,255,255,0.8);
  height: 100%;
  background-color: inherit;
  font-family: 'Londrina Solid', sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  margin: 0; /* Important for vertical align on mobile phones */
  display: inline-grid;
  grid-auto-flow: column;
  grid-template-columns: auto auto;
  align-items: center;
  margin-left: 5px;
}

/* Add a red background color to navbar links on hover */
.navbar a:hover, .dropdown:hover .dropbtn {
  /* background-color: red; */
}

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  padding: 3px;
}

/* Links inside the dropdown */
.dropdown-content div.season-option {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content div.season-option:hover:not(.selected) {
  background-color: #ddd;
}

div.season-option.selected {
  background-color: #333;
  color: white;
}

div.season-title {
  overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block;
}

/* Show the dropdown menu on hover */
.dropdown.active .dropdown-content {
  display: block;
}

@media only screen and (max-width: 671px) {
  .navbar {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: 
      "title title"
      "season episode";
  }

  .navbar h1 {
    justify-self: center;
  }

  #season-select {
    justify-self: center;
  }

  .dropdown-content {
    position: fixed;
    left: 0; 
  }
}
/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgb(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  top: -5px;
  right: 105%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
  position: absolute;
  z-index: 1;
  text-transform: capitalize;
  font-family: 'Londrina Solid', sans-serif;
  size: 1.3rem;
  font-weight: 100;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

/* Add arrow below tooltip */
.tooltip .tooltiptext::after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: 100%; /* To the right of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent black;
}

